.header__wrapper {
  width: 100%;
  height: 100vh;
  background: radial-gradient(100% 100% at 52.01% 53.93%, rgba(255, 255, 255, 9e-06) 0%, rgba(0, 0, 0, 0.045) 100%), #F4F4F4;
  margin: 0 auto;
  padding: 0 120px;
  box-sizing: border-box;
}

.header__wrapper-inner {
  height: 100%;
  width: 100%;
  background-image: url("img/GReeenco.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 70px 0;
  box-sizing: border-box;
}

.header__label {
  font-family: 'Big John';
  color: #7A797A;
  font-size: 34px;
}

.header__background-plant {
  z-index: 100;
  position: absolute;
  bottom: -3px;
  right: 110px;
}

.header__background-plant img {
  width: 50%;
}

.menu {
  display: flex;
  justify-content: space-between;
}

.menu-item:hover {
  opacity: .7;
  transition: .2s;
}
